import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { BasehttpService } from '../../utils/basehttp/basehttp.service';
import { environment } from 'src/environments/environment';
import { UtilService } from '../../utils/util/util.service';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root',
})
export class ProductService {
  private http: string = 'http://127.0.0.1:8000/api';

  constructor(
    private httpService: BasehttpService,
    private utilService: UtilService,
    private httpClient: HttpClient
  ) {}

  public listProducts(): Observable<any> {
    let httpUrl = `${this.http}/product`;

    return this.httpClient.get(httpUrl);
  }

  public listProductsByWhareHouse(idWhareHouse: Number): Observable<any> {
    let httpUrl = `${this.http}/product/?wharehouse=${idWhareHouse}`;

    return this.httpClient.get(httpUrl);
  }

  public listOrderByWhareHouse(idWhareHouse: Number): Observable<any> {
    let httpUrl = `${this.http}/order/?wharehouse=${idWhareHouse}`;

    return this.httpClient.get(httpUrl);
  }

  public addBot(body: object): Observable<any> {
    let httpUrl = `${this.http}/product/`;

    return this.httpClient.post(httpUrl, body);
  }

  public addOrder(body: object): Observable<any> {
    let httpUrl = `${this.http}/order/`;

    return this.httpClient.post(httpUrl, body);
  }

  public updateBot(id: string, body: object): Observable<any> {
    let httpUrl = `${this.http}/product/${id}/`;

    return this.httpClient.put(httpUrl, body);
  }
}
